.skills-container {
  min-height: 100vh;
  background:  #75420e;
  padding: 2rem 0;
}

.skills-header {
  text-align: center;
  margin-bottom: 2rem;
  padding: 0 2rem;
}

.skills-header h1 {
  font-size: 3rem;
  color: white;
  margin-bottom: 1rem;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.skills-header p {
  font-size: 1.2rem;
  color: rgba(255, 255, 255, 0.9);
  max-width: 600px;
  margin: 0 auto;
  line-height: 1.6;
}

/* Stats Section - now at the top */
.skills-stats {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin: 0 auto 2.5rem auto;
  background: rgba(255,255,255,0.12);
  border-radius: 16px;
  padding: 1.5rem 2rem;
  box-shadow: 0 2px 12px rgba(0,0,0,0.08);
  max-width: 900px;
}

.stat-item {
  text-align: center;
  color: white;
}

.stat-number {
  font-size: 2.2rem;
  font-weight: 700;
  color: #fff;
  text-shadow: 0 2px 8px rgba(0,0,0,0.18);
}

.stat-label {
  font-size: 1rem;
  color: #ffe6b3;
  margin-top: 0.3rem;
  letter-spacing: 0.5px;
}

/* Skills Categories - grid layout, smaller */
.skills-content {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
  align-items: flex-start;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
}

.skills-category {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border-radius: 16px;
  padding: 1.2rem 1rem;
  min-width: 0;
  margin-bottom: 0;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.category-header {
  text-align: center;
  margin-bottom: 1.2rem;
}

.category-title {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.7rem;
  font-size: 1.2rem;
  color: white;
  margin-bottom: 0.5rem;
  font-weight: 600;
}

.category-icon {
  font-size: 2rem;
  animation: float 3s ease-in-out infinite;
}

@keyframes float {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-10px); }
}

.category-description {
  color: rgba(255, 255, 255, 0.8);
  font-size: 1rem;
  max-width: 400px;
  margin: 0 auto;
}

/* Skills Grid */
.skills-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.2rem;
}

/* Skill Cards - smaller */
.skill-card {
  background: white;
  border-radius: 12px;
  padding: 1rem;
  text-align: center;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.08);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.skill-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(45deg, #75420e, #553b08);
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.skill-card:hover::before {
  transform: scaleX(1);
}

.skill-card:hover {
  transform: translateY(-6px);
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.13);
}

.skill-icon {
  width: 48px;
  height: 48px;
  margin: 0 auto 1rem;
  background: linear-gradient(45deg, #75420e, #553b08);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.3rem;
  color: white;
  transition: transform 0.3s ease;
}

.skill-card:hover .skill-icon {
  transform: scale(1.1) rotate(10deg);
}

.skill-name {
  font-size: 1.05rem;
  font-weight: 600;
  color: #333;
  margin-bottom: 0.6rem;
}

.skill-level {
  margin-bottom: 0.7rem;
}

.skill-level-text {
  font-size: 0.85rem;
  color: #666;
  margin-bottom: 0.3rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.skill-percentage {
  font-weight: 600;
  color: #75420e;
}

/* Progress Bar */
.progress-bar {
  width: 100%;
  height: 7px;
  background: #e1e5e9;
  border-radius: 10px;
  overflow: hidden;
  position: relative;
}

.progress-fill {
  height: 100%;
  background: linear-gradient(45deg, #75420e, #553b08);
  border-radius: 10px;
  transition: width 1.5s ease-in-out;
  position: relative;
}

.progress-fill::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.3), transparent);
  animation: shimmer 2s infinite;
}

@keyframes shimmer {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

.skill-description {
  color: #666;
  font-size: 0.85rem;
  line-height: 1.4;
  margin-top: 0.7rem;
}

/* Experience Level Badges */
.experience-badge {
  display: inline-block;
  padding: 0.2rem 0.7rem;
  border-radius: 13px;
  font-size: 0.7rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  margin-top: 0.4rem;
}

.beginner {
  background: #a67c3a;
  color: white;
}

.intermediate {
  background: #75420e;
  color: white;
}

.advanced {
  background: #553b08;
  color: white;
}

.expert {
  background: #3d2906;
  color: white;
}

/* Category-specific icon backgrounds */
.frontend-category .skill-icon {
  background: linear-gradient(45deg, #75420e, #a67c3a);
}

.backend-category .skill-icon {
  background: linear-gradient(45deg, #553b08, #75420e);
}

.database-category .skill-icon {
  background: linear-gradient(45deg, #3d2906, #553b08);
}

.tools-category .skill-icon {
  background: linear-gradient(45deg, #a67c3a, #75420e);
}

/* Responsive Design */
@media (max-width: 900px) {
  .skills-content {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  .skills-stats {
    flex-direction: column;
    gap: 1.2rem;
    padding: 1rem;
  }
}

@media (max-width: 768px) {
  .skills-header h1 {
    font-size: 2rem;
  }
  .category-title {
    font-size: 1rem;
  }
  .skills-category {
    padding: 1rem;
  }
  .skill-card {
    padding: 0.7rem;
  }
  .skill-icon {
    width: 36px;
    height: 36px;
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .skills-container {
    padding: 1rem 0;
  }
  .skills-content {
    padding: 0 0.5rem;
  }
  .skills-category {
    padding: 0.7rem;
  }
  .skills-grid {
    grid-template-columns: 1fr;
  }
  .category-title {
    flex-direction: column;
    gap: 0.3rem;
  }
}

/* Loading Animation for Progress Bars */
.skill-card.animate .progress-fill {
  animation: fillProgress 2s ease-out forwards;
}

@keyframes fillProgress {
  from { width: 0%; }
  to { width: var(--progress-width); }
}

/* Hover Effects for Categories */
.skills-category:hover {
  background: rgba(255, 255, 255, 0.15);
  transform: translateY(-3px);
  transition: all 0.3s ease;
}

/* Additional Visual Enhancements */
.skill-card .skill-icon::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle at center, rgba(255,255,255,0.3) 0%, transparent 70%);
  border-radius: 50%;
  top: 0;
  left: 0;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.skill-card:hover .skill-icon::after {
  opacity: 1;
}